<template>
    <div>
        <!-- 顶部的筛选 -->
        <classify :iden="1"></classify>

        <!-- 主题内容 -->
        <common :loading="loading" :error="error" :datas="pictureData"></common>
    </div>
</template>
<script>
    import Classify from '../component/classify.vue';
    import Common from '../component/common.vue';
    import OPLACE from '../../../oplace.js';
    export default {
        data() {
            return {
                pictureData: [],
                loading: false, // 加载中
                error: null, // 加载错误
            }
        },
        created() {
            this.getPictureOplace();
        },
        methods: {

            // 获取图片作品信息
            getPictureOplace() {
                this.pictureData = OPLACE.picture;
            }
        },
        components: {
            Classify,
            Common
        }
    }
</script>
<style lang="scss" rel="stylesheet/scss" scoped media="screen">
    .column {
        -webkit-column-count: 3;
        column-count: 3;
        -webkit-column-gap: 15px;
        column-gap: 15px;
    }
</style>



